@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,500i,700,700i,900&amp;display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
	/*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",		sans-serif;
	*/
	margin: 0;
	overflow-x: hidden;
	font-family: "Roboto", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-tap-highlight-color: transparent;
	background: #f9f9f9;
	transition: all 0.6s;
	-webkit-transition: all 0.6s;
	-moz-transition: all 0.6s;
	-ms-transition: all 0.6s;
	-o-transition: all 0.6s;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
	display: -webkit-flex;
	display: flex;
	margin: -20px; /* gutter size offset */
	width: auto;
}
.my-masonry-grid_column {
	margin: 10px; /* gutter size */
	background-clip: padding-box;
}
.my-masonry-grid_column > div {
	/* change div to reference your elements you put in <Masonry> */
	margin: 15px auto;
}

